import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';

const checkoutBtn = document.getElementById('checkout-btn');

checkoutBtn.addEventListener('click', function() {
   const updateTotal = () => {
    return document.getElementById('total__item').value }
  console.log(updateTotal())
  let price = updateTotal() * .00159;

  ReactDOM.render(
    <React.StrictMode>
      <App price={price}/>
    </React.StrictMode>,
    document.getElementById('cashtab')
  );
})


